.placeholder-page {
  padding: 80px 20px;
  min-height: 50vh;
}

.placeholder-page__title {
  font-size: clamp(1.5rem, 4vw, 2rem);
  color: #1e3c72;
  margin-bottom: 16px;
}

.placeholder-page__text {
  color: #666;
  font-size: 1.1rem;
}

@media (max-width: 480px) {
  .placeholder-page {
    padding: 48px 16px;
  }

  .placeholder-page__title {
    font-size: 1.5rem;
  }

  .placeholder-page__text {
    font-size: 1rem;
  }
}
